"use client"

import { useState, useEffect, useRef } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { 
  ChevronLeft, Send, Sparkles, Loader2, LogOut, 
  MessageSquare, Settings, ShieldCheck, 
  Lightbulb, Target, Wallet, Zap
} from "lucide-react"

import { Client } from "@stomp/stompjs"
import SockJS from "sockjs-client"

export default function AIChatPage() {
  const router = useRouter()
  const [currentUserId, setCurrentUserId] = useState<string | null>(null)
  const [userName, setUserName] = useState("")
  const [messages, setMessages] = useState<any[]>([])
  const [inputValue, setInputValue] = useState("")
  const [isTyping, setIsTyping] = useState(false)
  const [isConnected, setIsConnected] = useState(false)
  const [currentRoomId, setCurrentRoomId] = useState<string | null>(null)
  
  const stompClient = useRef<Client | null>(null)
  const scrollRef = useRef<HTMLDivElement>(null)

  const API_BASE = "http://sparta-nextme.xyz:18080"

  useEffect(() => {
    const token = localStorage.getItem("accessToken")
    const userId = localStorage.getItem("userId")
    const name = localStorage.getItem("name") || "ÏÇ¨Ïö©Ïûê"
    setCurrentUserId(userId); setUserName(name)

    if (!token) { router.push("/login"); return }

    const client = new Client({
      brokerURL: `ws://sparta-nextme.xyz:18080/ws/chat`,
      connectHeaders: { Authorization: `Bearer ${token}` },
      webSocketFactory: () => new SockJS(`${API_BASE}/ws/chat`),
    })

    client.onConnect = () => {
      setIsConnected(true)
      initChatRoom(token, userId)
    }
    client.activate()
    stompClient.current = client
    return () => { if (stompClient.current) stompClient.current.deactivate() }
  }, [router])

  const initChatRoom = async (token: string, userId: string | null) => {
    try {
      const res = await fetch(`${API_BASE}/v1/chats/room?roomType=AI`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      const aiRooms = data.rooms || data.result || (Array.isArray(data) ? data : [])
      let roomId = aiRooms?.[0]?.chatRoomId || aiRooms?.[0]?.id

      if (!roomId) {
        const createRes = await fetch(`${API_BASE}/v1/chats/room`, {
          method: "POST",
          headers: { "Content-Type": "application/json", "Authorization": `Bearer ${token}` },
          body: JSON.stringify({ roomType: "AI", userId })
        })
        const newData = await createRes.json()
        roomId = newData.chatRoomId || newData.result?.chatRoomId
      }

      if (roomId) {
        setCurrentRoomId(roomId)
        const msgRes = await fetch(`${API_BASE}/v1/chats/message/${roomId}?size=20`, {
          headers: { "Authorization": `Bearer ${token}` }
        })
        const msgData = await msgRes.json()
        const fetchedMsgs = Array.isArray(msgData) ? msgData : (msgData.messages || msgData.result || [])
        setMessages(fetchedMsgs.length === 0 ? [{ senderId: "AI", content: `Î∞òÍ∞ÄÏõåÏöî, ${userName}Îãò! Ïû¨Î¨¥ Î∂ÑÏÑùÏùÑ ÏãúÏûëÌï†ÍπåÏöî?`, createdAt: new Date().toISOString() }] : fetchedMsgs.reverse())
        subscribeToRoom(roomId)
        scrollToBottom()
      }
    } catch (e) { console.error(e) }
  }

  const subscribeToRoom = (roomId: string) => {
    if (stompClient.current?.connected) {
      stompClient.current.subscribe(`/topic/chat.room.${roomId}`, (message) => {
        const newMessage = JSON.parse(message.body)
        setMessages((prev) => [...prev, newMessage]); setIsTyping(false); scrollToBottom()
      })
    }
  }

  const handleSend = (manualText?: string) => {
    const textToSend = manualText || inputValue
    if (!textToSend.trim() || !stompClient.current?.connected || !currentRoomId) return
    stompClient.current.publish({ destination: `/app/chat.send/${currentRoomId}`, body: JSON.stringify({ content: textToSend, roomType: "AI" }) })
    setInputValue(""); setIsTyping(true); scrollToBottom()
  }

  const handleLeaveRoom = async () => {
    if (!currentRoomId || !confirm("ÎåÄÌôîÎ•º Ï¢ÖÎ£åÌïòÏãúÍ≤†ÏäµÎãàÍπå?")) return
    const token = localStorage.getItem("accessToken")
    try {
      await fetch(`${API_BASE}/v1/chats/room/${currentRoomId}/leave`, {
        method: "POST",
        headers: { "Authorization": `Bearer ${token}`, "Content-Type": "application/json" },
        body: JSON.stringify({ userId: currentUserId })
      })
    } finally { router.push("/dashboard") }
  }

  const scrollToBottom = () => { setTimeout(() => { if (scrollRef.current) scrollRef.current.scrollTop = scrollRef.current.scrollHeight }, 100) }

  return (
    <div className="min-h-screen bg-white text-foreground flex flex-col font-sans overflow-hidden">
      {/* 1. Header: Ìà¨Î™ÖÎèÑ ÏûàÎäî ÌôîÏù¥Ìä∏ Ïú†ÏßÄ */}
      <header className="border-b border-slate-100 bg-white/80 backdrop-blur-md sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <div className="flex items-center gap-4">
            <Button variant="ghost" size="icon" className="hover:bg-slate-50" onClick={() => router.push("/dashboard")}><ChevronLeft className="h-5 w-5" /></Button>
            <Link href="/" className="flex items-center gap-2">
              <Image src="/logo.png" alt="Next Me" width={100} height={28} className="h-6 w-auto" priority />
            </Link>
          </div>
          <div className="flex items-center gap-3">
            <div className="hidden md:flex flex-col items-end mr-2">
              <span className="text-[11px] font-bold text-slate-400 uppercase tracking-tighter">AI Assistant</span>
              <span className="text-[10px] font-bold text-primary leading-none">Status: {isConnected ? 'Active' : 'Offline'}</span>
            </div>
            <Avatar className="h-9 w-9 border-2 border-primary/10 shadow-sm"><AvatarFallback className="bg-primary/5 text-xs font-bold text-primary">{userName?.charAt(0)}</AvatarFallback></Avatar>
          </div>
        </div>
      </header>

      {/* 2. Main Layout: Î∞∞Í≤ΩÏùÑ ÏôÑÏ†ÑÌûà ÌôîÏù¥Ìä∏/Îß§Ïö∞ Î∞ùÏùÄ Î∏îÎ£®Î°ú Î≥ÄÍ≤Ω */}
      <div className="container mx-auto px-4 py-6 flex-1 flex gap-6 overflow-hidden" style={{ height: "calc(100vh - 64px)" }}>
        
        {/* Left Sidebar: Ìà¨Î™Ö Ïπ¥Îìú Ïä§ÌÉÄÏùº */}
        <aside className="w-72 shrink-0 hidden xl:flex flex-col gap-4">
          <Card className="flex-1 border-slate-100 bg-slate-50/40 shadow-none p-5 space-y-6">
            <div className="space-y-1">
              <h3 className="text-[11px] font-black text-slate-400 uppercase tracking-[0.1em] flex items-center gap-2">
                <Zap className="h-3.5 w-3.5 text-yellow-500 fill-yellow-500" /> Smart Guide
              </h3>
              <p className="text-sm font-bold text-slate-700">AIÏôÄ Ïù¥Î†áÍ≤å ÎåÄÌôîÌï¥Î≥¥ÏÑ∏Ïöî</p>
            </div>
            <div className="space-y-3">
              {[
                { icon: Target, label: "ÎÇ¥ Î™©Ìëú Îã¨ÏÑ± Í∞ÄÎä•ÏÑ± ÌôïÏù∏", color: "text-blue-500" },
                { icon: Wallet, label: "ÏßÄÏ∂ú Ìå®ÌÑ¥ Î∂ÑÏÑù ÏöîÏ≤≠ÌïòÍ∏∞", color: "text-green-500" },
                { icon: Lightbulb, label: "Ï†àÏÑ∏ Î∞è ÏûêÏÇ∞Í¥ÄÎ¶¨ ÌåÅ Î∞õÍ∏∞", color: "text-orange-500" }
              ].map((item, i) => (
                <div key={i} className="flex items-center gap-3 p-3 rounded-xl bg-white border border-slate-100 shadow-sm hover:border-primary/30 transition-all cursor-default">
                  <item.icon className={`h-4 w-4 ${item.color}`} />
                  <span className="text-xs font-bold text-slate-600">{item.label}</span>
                </div>
              ))}
            </div>
            <div className="pt-4 border-t border-slate-200">
               <Button variant="ghost" className="w-full justify-start text-[11px] font-bold text-slate-400 hover:text-destructive" onClick={handleLeaveRoom}><LogOut className="h-3.5 w-3.5 mr-2" /> ÎåÄÌôîÎ∞© ÎÇòÍ∞ÄÍ∏∞</Button>
            </div>
          </Card>
        </aside>

        {/* 3. Main Chat Window: ÌöåÏÉâ Î∞∞Í≤ΩÏùÑ ÏÇ≠Ï†úÌïòÍ≥† Í∑∏ÎùºÎç∞Ïù¥ÏÖò Ï†ÅÏö© */}
        <Card className="flex-1 flex flex-col border-none shadow-[0_0_40px_rgba(0,0,0,0.03)] bg-white overflow-hidden relative">
          {/* Ï±ÑÌåÖÏ∞Ω ÎÇ¥Î∂Ä Î∞∞Í≤Ω Îç∞ÏΩîÎ†àÏù¥ÏÖò */}
          <div className="absolute top-[-10%] right-[-5%] w-[400px] h-[400px] bg-primary/5 rounded-full blur-[100px] pointer-events-none" />
          <div className="absolute bottom-[-10%] left-[-5%] w-[300px] h-[300px] bg-blue-50/50 rounded-full blur-[80px] pointer-events-none" />

          {/* Chat List Area */}
          <div ref={scrollRef} className="flex-1 overflow-y-auto p-6 space-y-6 scrollbar-hide z-10">
            <div className="max-w-3xl mx-auto">
              {messages.map((msg, idx) => {
                const isMine = msg.senderId === currentUserId
                return (
                  <div key={idx} className={`flex mb-6 ${isMine ? "justify-end" : "justify-start"} animate-in fade-in slide-in-from-bottom-2 duration-300`}>
                    <div className={`flex flex-col space-y-2 max-w-[80%] ${isMine ? "items-end" : "items-start"}`}>
                      {!isMine && (
                        <div className="flex items-center gap-2 ml-1">
                          <div className="h-6 w-6 rounded-lg bg-primary flex items-center justify-center shadow-md shadow-primary/20"><Sparkles className="h-3 w-3 text-white" /></div>
                          <span className="text-[11px] font-black text-slate-800 uppercase tracking-tighter">Next Me AI</span>
                        </div>
                      )}
                      <div className={`p-4 text-sm font-bold leading-relaxed shadow-sm ${
                        isMine 
                          ? "bg-primary text-white rounded-2xl rounded-tr-none shadow-blue-200/50" 
                          : "bg-white border border-slate-100 text-slate-700 rounded-2xl rounded-tl-none"
                      }`}>
                        {msg.content}
                      </div>
                      <span className="text-[9px] font-bold text-slate-300 px-1 uppercase">{msg.createdAt ? new Date(msg.createdAt).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }) : ""}</span>
                    </div>
                  </div>
                )
              })}
              {isTyping && (
                <div className="flex justify-start">
                  <div className="bg-slate-100/50 rounded-2xl p-4 flex gap-1.5 items-center">
                    <span className="w-1.5 h-1.5 bg-primary/40 rounded-full animate-bounce" />
                    <span className="w-1.5 h-1.5 bg-primary/60 rounded-full animate-bounce [animation-delay:0.2s]" />
                    <span className="w-1.5 h-1.5 bg-primary/80 rounded-full animate-bounce [animation-delay:0.4s]" />
                  </div>
                </div>
              )}

              {/* Ï∂îÏ≤ú ÏßàÎ¨∏ Ïπ¥Îìú (ÌôîÏù¥Ìä∏ ÌÖåÎßà) */}
              {messages.length <= 1 && (
                <div className="grid grid-cols-1 md:grid-cols-2 gap-3 mt-10">
                  {[
                    "üè† ÎÇ¥ Ïßë ÎßàÎ†®ÏùÑ ÏúÑÌïú ÏûêÍ∏à Í≥ÑÌöç",
                    "üí∞ ÏÜåÎπÑ ÏäµÍ¥Ä Î∂ÑÏÑù Î∞è Ï†àÏïΩ ÌåÅ",
                    "üìä ÎÇòÏùò Ìà¨Ïûê ÏÑ±Ìñ• ÌôïÏù∏ÌïòÍ∏∞",
                    "üí≥ Ïã†Ïö©Ï†êÏàò Ïò¨Î¶¨Îäî Î∞©Î≤ï ÏïàÎÇ¥"
                  ].map((q, i) => (
                    <button
                      key={i}
                      onClick={() => handleSend(q.slice(3))}
                      className="p-4 text-left bg-white border border-slate-100 rounded-2xl hover:border-primary hover:bg-primary/[0.02] hover:shadow-md transition-all group"
                    >
                      <span className="text-xs font-black text-slate-700 group-hover:text-primary transition-colors">{q}</span>
                    </button>
                  ))}
                </div>
              )}
            </div>
          </div>

          {/* 4. Input Area: ÍπîÎÅîÌïú ÌôîÏù¥Ìä∏ Í≥†Ï†ï */}
          <div className="p-6 bg-white/80 backdrop-blur-md border-t border-slate-50 z-20">
            <form onSubmit={(e) => { e.preventDefault(); handleSend(); }} className="max-w-3xl mx-auto flex gap-3 relative">
              <Input 
                value={inputValue}
                onChange={(e) => setInputValue(e.target.value)}
                placeholder="Í∏àÏúµ ÏßàÎ¨∏ÏùÑ ÏûÖÎ†•Ìï¥ Î≥¥ÏÑ∏Ïöî..." 
                className="flex-1 h-14 bg-slate-50/50 border-slate-200 focus:border-primary/30 focus:ring-primary/10 rounded-2xl pl-6 pr-14 font-bold text-[13px] placeholder:text-slate-400"
              />
              <Button 
                type="submit" 
                size="icon" 
                className="absolute right-1.5 top-1.5 h-11 w-11 rounded-xl bg-primary hover:bg-primary/90 shadow-lg shadow-primary/20 transition-transform active:scale-95" 
                disabled={!inputValue.trim()}
              >
                <Send className="h-5 w-5 text-white" />
              </Button>
            </form>
            <p className="text-[10px] text-center text-slate-300 mt-4 font-bold tracking-widest uppercase">Next Me AI ‚Ä¢ Intelligent Financial Assistant</p>
          </div>
        </Card>
      </div>

      <style jsx global>{`
        .scrollbar-hide::-webkit-scrollbar { display: none; }
        .scrollbar-hide { -ms-overflow-style: none; scrollbar-width: none; }
      `}</style>
    </div>
  )
}